<template>
  <section>

    <h2 class="folder" @click="show = !show">✉ {{name}}<span v-bind:style=" show ? {display: 'none'} : {display: 'inline-block'}">/ ...</span></h2>

    <div v-show="show" class="show">
      <template v-for="folder in folders">
        <FolderView :name="folder.name" :folders="folder.folders" :files="folder.files"/>
      </template>

      <ul>
        <template v-for="file in files">
          <FileView :name="file.name" :type="file.type" :length="file.length"/>
        </template>
      </ul>
    </div>

  </section>
</template>

<script>

import FileView from "./FileView";
export default {
  name: 'FolderView',
  components: {FileView},
  props: {
    name: String,
    folders: Array,
    files: Array,
    show: {
      type: Boolean,
      default: false
    }
  }
}

</script>

<style scoped lang="scss">

section {
  padding-left: 15px;
  
  span {
    margin-left: 7px;
    font-size: 14px;
  }

  .show {
    border-left: 1px dotted #666;
  }
}

</style>
