<template>
  <li class="file">
    <p>{{name}}</p>
    <p>{{type}}</p>
    <p>{{length}} <span>byte</span></p>
  </li>
</template>

<script>
export default {
  name: 'FileView',
  props: {
    name: String,
    type: String,
    length: String
  },
}
</script>

<style scoped lang="scss">

li {
  padding: 7px 15px;
  list-style: none;

  @media (min-width: 800px) {
  display: grid;
  grid-template-columns: 1fr 150px 100px;
  justify-content: space-between;
  grid-gap: 50px;
  }

  &:last-child {
    margin-bottom: 15px;
  }

  &:hover {
    background: #00000020;
  }

  p {
    color: #ddd;

    &:nth-child(2), &:nth-child(3) {
      color: #aaa;
      @media (max-width: 800px) {display: none}
    }
    
    @media (min-width: 800px) {
      &:nth-child(3) {
        position: relative;
        text-align: right;
        padding-right: 34px;
        
        span {
          position: absolute;
          font-size: 13px;
          right: 0;
        }
      }
    }
  }
}

</style>
